import React from 'react';
import { BrowserRouter, Route, Switch, Redirect } from "react-router-dom";

import OTPAuth from './pages/OTPAuth';
import Checkout from './pages/Checkout';

import './App.css';

class App extends React.Component<{}, { user: object }> {
  constructor(props: any) {
    super(props);
    this.state = {
      user: {}
    }
  }

  setUser = (user: object) => {
    this.setState({ user });
  }
      //@ts-ignore
   PrivateRoute = ({ component: Component, ...rest }) => {
    console.log("Object.keys(object1)",this.state.user);

    return (<Route {...rest} render={(props) => (
      Object.keys(this.state.user).length >0
        ? <Component {...props} />
        : <Redirect to='/' />
    )} />
    )};

  render() {
    return (
      <BrowserRouter basename={process.env.PUBLIC_URL}>
         <div className="App">
          <header className="App-header">
      <Switch>  
         <Route exact path='/'  render={() => <OTPAuth setUser={this.setUser}/>} />
     
        <this.PrivateRoute path='/checkout' component={Checkout} />
        
      </Switch>
        </header>
       </div>
      </BrowserRouter>

    )
    // return (
    //   <Router>
    //     <div className="App">
    //       <header className="App-header">
    //         <Switch>
    //           <Route path="/checkout" render={() => <Checkout/>} />
    //           <Route path="/" exact render={() => <OTPAuth setUser={this.setUser}/>} />
    //         </Switch>
    //       </header>
    //     </div>
    //   </Router>

    // );
  }
}

export default App;
